<template>
    <div id="app">
        <Fheader v-bind:title="header_title"></Fheader>
        <div class="steps-bar">
            <div class="steps-wrapper">
                <div class="steps-flex">
                    <div class="steps-item">
                        <div class="steps-dot"></div>
                        <div class="steps-text">肿瘤</div>
                    </div>
                    <div class="steps-item steps-item-cur">
                        <div class="steps-dot"></div>
                        <div class="steps-text">肝功能</div>
                    </div>
                    <div class="steps-item">
                        <div class="steps-dot"></div>
                        <div class="steps-text">肾功能</div>
                    </div>
                </div>
            </div>
        </div>
        <div class="doctor-form">
            <template v-for="(liver, index) in index_data">
                <div class="piece piece-with-date">
                    <p>
                        {{ liver.index }}
                    </p>
                    <div class="piece-date-picker" v-bind:class="'piece-date-picker-'+index">
                        <template v-if="liver.date == ''">
                            <div class="picker-not">
                                <i class="icon-calendar"></i>
                                <span>请选择出报告时间</span>
                            </div>
                        </template>
                        <template v-else>
                            <div class="picker-yes">
                                <span>已选择</span>
                                <span>“{{ liver.date }}”</span>
                            </div>
                        </template>
                    </div>
                </div>
                <div class="info-card">
                    <input type="" name="" class="input-info" v-model="liver.value" placeholder="请填写指标信息">
                </div>
            </template>
        </div>
        <div class="ft-bar-skip-next">
            <div class="ft-bar-tab" v-on:click="skipHandler">跳过</div>
            <div class="ft-bar-tab" v-on:click="nextHandler">下一步（2/5）</div>
        </div>
		<transition name="fade">
			<Toast v-bind:msg="toast_text" v-if="toastshow"></Toast>
		</transition>
    </div>
</template>
<script>
import Fheader from '../../components/common/Fheader'
import Toast from '../../components/common/Toast'
import store from '../../store/store.js'
import utils from '../../utils/utils.js'

export default {
    name: 'app',
    data: function(){
    	return {
    		toastshow: false,
    		toast_text: '',
            header_title: '关注指标',
            nextlink: 'http://www.baidu.com',
            picker_arr: [],
            index_data: [
                {
                    index: '总胆红素 TBIL',
                    value: '',
                    date: ''
                },
                {
                    index: '直接胆红素 DBIL',
                    value: '',
                    date: ''
                },
                {
                    index: '间接胆红素 IBIL',
                    value: '',
                    date: ''
                },
                {
                    index: '总蛋白 TP',
                    value: '',
                    date: ''
                },
                {
                    index: '白蛋白 Alb',
                    value: '',
                    date: ''
                },
                {
                    index: '谷丙转氨酶 ALT',
                    value: '',
                    date: ''
                },
                {
                    index: '谷草转氨酶 AST',
                    value: '',
                    date: ''
                },
                {
                    index: '碱性磷酸酶 ALP',
                    value: '',
                    date: ''
                }
            ]
    	}
    },
    mounted: function(){
        var that = this;
        that.initLiverFunction();
    },
    methods:{
    	showToast: function(){
    		var that = this;
    		if(that.toastshow){
    			return;
    		}
    		that.toastshow = true;
    		setTimeout(function(){
    			that.toastshow = false;
    		}, 3000);
    	},
        initLiverFunction: function(){
            var that = this;
            store.get_index('liver').then(function(data){
                console.log(data);
                if(data.data.code == 0){
                    var tmp_arr = [];
                    for(var i=0;i<data.data.data.length;i++){
                        tmp_arr[i] = {
                            index: data.data.data[i].name,
                            value: '',
                            date: ''
                        }
                    }
                    that.index_data = tmp_arr;
                    that.$nextTick(function(){
                        that.initEvent();
                    });
                }else{
                    that.toast_text = data.data.message;
                    that.showToast();
                }
            }).catch(function(error){
                console.log(error);
            });
        },
        initEvent: function(){
            var that = this;
            for(var i=0;i<that.index_data.length;i++){
                (function(i){
                    var trigger = '.piece-date-picker-'+i;
                    var tmp = {
                        picker: new MobileSelectDate(),
                        picked: false
                    }
                    that.picker_arr[i] = tmp;
                    that.picker_arr[i].picker.init({
                        trigger: trigger,
                        value: '2014/06/07',
                        min:'1990/01/01',
                        position:'bottom',
                        callback:function(){
                            var picked_time = $('.piece-date-picker-'+i).attr('data-value');
                            that.index_data[i].date = picked_time.replace(/\,/g,'-');
                            that.picker_arr[i].picked = true;
                            for(var j=0;j<that.picker_arr.length;j++){
                                (function(j, picked_time){
                                    if(!that.picker_arr[j].picked){
                                        var date_tmp_arr = picked_time.split(',');
                                        that.picker_arr[j].picker.value = date_tmp_arr;
                                    }
                                })(j, picked_time);
                            }
                        }
                    });
                })(i);
            }
        },
        nextHandler: function(){
            var that = this;
            var has_input = false;
            for(var i=0;i<that.index_data.length;i++){
                if(utils.trim(that.index_data[i].value)){
                    if(!that.index_data[i].date){
                        that.toast_text = '第'+(i+1)+'项'+that.index_data[i].index+'报告时间还未指定！';
                        that.showToast();
                        return;
                    }
                }
                if(utils.trim(that.index_data[i].date)){
                    if(!that.index_data[i].value){
                        that.toast_text = '第'+(i+1)+'项'+that.index_data[i].index+'还未填写！';
                        that.showToast();
                        return;
                    }
                }
                if(utils.trim(that.index_data[i].value) && utils.trim(that.index_data[i].date)){
                    has_input = true;
                }
            }
            if(!has_input){
                return ;
            }else{
                store.liver_function(that.index_data).then(function(data){
                    console.log(data);
                    if(data.data.code == 0){
                        
                    }else{
                        that.toast_text = data.data.message;
                        that.showToast();
                    }
                }).catch(function(error){
                    console.log(error);
                });
            }
        },
        skipHandler: function(){
            location.href = store.site_links.renal_function;
        }
    },
    components: {
    	Fheader,
        Toast
    }
}
</script>

<style>
@import '../../assets/css/common.css';
@import '../../assets/css/date-picker-bottom.css';
.doctor-form{
    padding: 10px 15px 60px 15px;
}
.steps-bar{
    position: relative;
    margin-top: 10px;
    padding: 10px 15px;
}
.steps-wrapper{
    border-top: 2px solid #ccc;
}
.steps-flex{
    display: flex;
    justify-content: space-around;
    padding: 0px 30px;
}
.steps-item{
    min-width: 30px;
    text-align: center;
    color: #ccc;
    font-size: 1.4rem;
}
.steps-item-cur{
    color: #49ced4;
    font-size: 1.6rem;
}
.steps-dot{
    width: 6px;
    height: 6px;
    background: #ccc;
    border-radius: 50%;
    margin: -4px auto 5px auto;
}
.steps-item-cur .steps-dot{
    width: 8px;
    height: 8px;
    background: #49ced4;
    margin: -5px auto 5px auto;
}
</style>



